const routers = {	
	home:{
		redirect:'home',
		file:'home/index',		
		title:'首页',		
		sub:{
			home:{
				title:'首页',
				show_footer: true
			},			
			homework: {
				title: '作业列表',
				file: 'home/homework'
			},
			homework_chart: {
				path: 'homework/chart',
				title: '作业成长曲线'
			},
			homework_list: {
				path: 'homework/list',
				title: '作业列表'
			},
			homework_publish: {
				path: 'homework/publish',
				title: '作业发表记录'
			},
			homework_publish_detail:{
				path: 'homework/publish/:id',
				file: 'home/homework/publish/detail',
				title: '发表记录详情'
			},
			homework_detail: {
				path: 'homework/list/:id',
				file: 'home/homework/detail',
				title: '作业详情'
			},
			homework_add: {
				path: 'homework_add',
				file: 'home/homework/add',
				title: '布置作业'
			},
			prepare: '我的备课',
			prepare_detail: {
				path: 'prepare/:id',
				title: '备课详情',
				file: 'home/prepare/detail'
			},
			rollcall:'点名',
			schedule:'课表',
			schedule_detail: {
				path: 'schedule/:id',
				file: 'home/schedule/detail',
				title: '排课详情'
			},
			news:'通知',
			news_detail:{
				path: 'news/:id',
				file: 'home/news/detail',
				title: '通知详情'
			},
			comment: '课评',
			comment_detail: {
				path: 'comment/:id',
				file: 'home/comment/detail',
				title: '课评详情'
			},
			artwork: '作品列表',
			artwork_detail: {
				path: 'artwork/:id',
				file: 'home/artwork/detail',
				title: '作品详情',
				public: true
			},
			exam: '考试成绩',
			exam_detail: {
				path: 'exam/:id',
				file: 'home/exam/detail',
				title: '考试成绩'
			},
			medias: '我的文件',
			growth_contrast: '成长对比',
			growth_contrast_detail: {
				path: 'growth_contrast/:id',
				file: 'home/growth_contrast/detail',
				title: '对比详情'
			},
			growth_contrast_add: '添加成长对比',
		}
	},
	teaching: {
		redirect: 'teaching',
		file: 'teaching/index',
		title: '教学',		
		sub: {
			teaching: {
				title: '教学',
				show_footer: true
			}
		}
	},
	education: {
		redirect: 'education',
		file: 'education/index',
		title: '教育',
		sub: {
			education: {
				title:'教育',
				show_footer: true
			}
		}
	},
	recruit: {
		redirect: 'list',
		file: 'recruit/index',
		title: '客户名单',
		sub: {
			list: '客户名单',
			add: '添加客户',
			detail: {
				path: 'recruit/:id',
				file: 'recruit/detail',
				title: '客户详情'
			},
			following: '跟进记录',
			follow_add: '添加跟进',
			follow_detail: {
				path: 'following/:id',
				file: 'recruit/follow_detail',
				title: '跟进记录'
			},
			audition: '试听管理',
			channel: '市场渠道',
			clue: {
				path: 'channel/:id',
				file: 'recruit/clue',
				title: '市场名单'
			}
		}
	},
	report: {
		redirect: 'report',
		file: 'report/index',
		title: '报表',
		sub: {
			output: '我的产出',
			on: '在读学员统计报表',
			income: '课耗报表',
			incomeandexpend: '收支总表',
			customer: '招生总表',
			performance: '业绩报表',
			attendance: '考勤总表',
			dailyreport: '运营周报',
			report: {
				title:'报表',
				show_footer: true
			}
		}
	},
	class: {
		redirect: 'class',
		file: 'class/index',
		title: '班级',
		sub: {
			class: {
				title:'班级',
				show_footer: true	
			},
			service: {
				title: '班级服务',
				path: 'service/:id',				
				file: 'class/class/service'
			},
			class_detail: {
				path: 'class/:id',
				file: 'class/class/detail',
				title: '班级详情'
			}
		}
	},
	student: {
		redirect: 'student',
		file: 'student/index',
		sub: {
			student: {
				title: '学员',
				show_footer: true
			},
			service: {
				title: '学员服务',
				path: 'service/:id',				
				file: 'student/student/service'
			},
			student_detail: {
				path: 'student/:id',
				file: 'student/student/detail',
				title: '学员详情'
			}
		}
	},
	my: {
		redirect: 'my',
		file: 'my/index',
		title: '我的',
		sub: {
			my: {
				title:'我的',
				show_footer: true,
//				wxauth: true
			}
		}
	}		
}
export default {
	login:{
		sync:true,
		public:true,
		title:'登录'
	},
	logout:{
		sync:true,
		public:true,
		title:'退出登录'
	},
	forgotpwd:{
		sync:true,
		public:true,
		title:'忘记密码'
	},
	qrsign:{
		path: '/qrsign',
		sync:true,
		public:true,
		title:'录入市场名单'
	},
	comment: {
		path: '/reviews/:id',
		sync:true,
		public:true,
		title:'课评详情'
	},
	teacherintroduce:{
		path: '/teacher/:id',
		sync:true,
		public:true,
		title:'教师详情'
	},
	contrastdetail: {
		path:'/growth_contrast/:id',
		sync:true,
		public:true,
		title:'成长对比详情'
	},
	root:{
		path:'/',
		redirect:'/home/home',
		title:'首页',
		file:'layout',
		sub:routers
	}	
}


